import logo from '../logo.svg';
import '../App.css';
import '../Default.css'
import React from 'react';
import TopHeader from './TopHeader';
import BaseInfo from './BaseInfo'
import StackInfo from './StackInfo'
import WorkExperieceInfo from './WorkExperienceInfo';
import ContactsInfo from './ContactsInfo';
import LanguageChange from './LanguageChange';
import LanguageTitle from '../LanguageTitle';
import ProgressLoad from './ProgressLoad';
import ErrorDialog from './ErrorDialog';
import AppBar from './AppBar';

class Resume extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      isEnglish: this.props.isEnglish,
      isLoad: false,
      isError: false,
      errorMsg: undefined
    }
    this.localToRus = this.localToRus.bind(this)
    this.localToEnglish = this.localToEnglish.bind(this)
    this.languageTitle = LanguageTitle
  }

  componentDidMount(){
    window.onload = () => {
      (this.hashIsEmpty()) ? this.loadResume() : this.nickFromHashToLoad()
      this.hashChange()
    }
  }

  localToRus(){
    this.state.isEnglish = false
    this.setState(this.state)
  }
  localToEnglish(){
    this.state.isEnglish = true
    this.setState(this.state)
  }

  changeCallback() {
    return {
      toRussian: this.localToRus,
      toEnglish: this.localToEnglish
    }  
  }

  addJsonToState(json){
     this.state.json = json
     this.setState(this.state)
  }

  unError(){
    this.state.isError = false
  }
  unLoad(){
    this.state.isLoad = true
  }

  addErrorMsg(msg){
    this.state.isError = true
    this.state.errorMsg = msg
    this.setState(this.state)
  }

  hashChange(){
    window.addEventListener('hashchange', () => {
      this.nickFromHashToLoad()
  })
  }

  nickFromHashToLoad(){
    console.log("nick from cache to load")
    this.loadResume(window.location.hash.slice(1, window.location.hash.length))
  }

  hashIsEmpty(){
    return window.location.hash.length == 0
  }

  loadResume(nickName){
    console.log("loadResume "+nickName)
    let url = 
    (nickName === undefined) ? "https://raw.githubusercontent.com/mrtwon/resume/main/resume.json"
    : `https://raw.githubusercontent.com/${nickName}/resume/main/resume.json`
     fetch(url)
     .then(response => response.json())
     .then(json => {
        this.addJsonToState(json)
        this.unError()
        this.unLoad()
     })
     .catch(e => this.addErrorMsg("     "))
  }

  render(){
    if(this.state.isError){
        return <div><ErrorDialog errorMsg={this.state.errorMsg}/></div>
    }
    if(!this.state.isLoad){
      return <div><ProgressLoad/></div>
    }
    if(this.state.isLoad && !this.state.isError){
      let currentResume = (this.state.isEnglish ? this.state.json.en : this.state.json.ru)
      let currentLanguage = (this.state.isEnglish ? this.languageTitle.en : this.languageTitle.ru)  
      return <div>
      <AppBar
        contacts={currentResume.contacts}
        isEnglish={this.state.isEnglish}
        changeCallback={this.changeCallback()}
        classNameSelector='language-selector-app-bar'
        classNameFlag='flag-app-bar'
        />
      <TopHeader titleOnLanguage={currentLanguage}/>
      <BaseInfo resume={currentResume} titleOnLanguage={currentLanguage}/>
      <StackInfo resume={currentResume}/>
      <WorkExperieceInfo resume={currentResume}/>
      <ContactsInfo resume={currentResume}/>
      <LanguageChange 
            isEnglish={this.state.isEnglish}
            changeCallback={this.changeCallback()}
            classNameSelector='flag-selector'
            classNameFlag='flag'
            />
      </div>
    }
  }
}

export default Resume;