import React from 'react';
import '../App.css';
import OneStack from './OneStack';
class StackInfo extends React.Component{
    render(){
        let stackArray = this.props.resume.stack
        return(
            <div id="my-stack-div">
                {
                    stackArray.map( oneStack => <OneStack stack={oneStack}/> )
                }
            </div>
        )
    }
}
export default StackInfo;