import React from 'react'
import '../App.css';

class OneWorkExperience extends React.Component{
    render(){
        let project = this.props.experience.project;
        let description = this.props.experience.description;
        let link = this.props.experience.link;
        return(
            <div className='out-test'>
	          		<div className='inner-test'>
		       			<h2>{project}</h2>
		       			<p>{description}</p>
                        <div className='project-site'><a href={link}></a></div>
	          		</div>
	        	</div>
        )
    }
}

export default OneWorkExperience;