import React from 'react';
import '../App.css';

class BaseInfo extends React.Component{
    render(){
		let name = this.props.resume.name;
        let age = this.props.resume.age
        let city = this.props.resume.city
		let avatar = this.props.resume.avatar
		let titleOnLanguage = this.props.titleOnLanguage
		let avatarStyle = { backgroundImage: `url(${avatar})` }
        return(
            <div className="base-info">
		        <div id="m-photo" style={avatarStyle}></div>
		        <div className="base-info-label-div">
			        <h3>{titleOnLanguage.generalInfo}</h3>
		        </div>
		        <div className="base-info-description-div">
			        <p id="me-name"><span>{titleOnLanguage.name}</span>{name}</p>
			        <p id="me-age"><span>{titleOnLanguage.age}</span>{this.calcYo(age)}</p>
			        <p id="me-city"><span>{titleOnLanguage.city}</span>{city}</p>
		        </div>
	        </div>
        )
    }
	calcYo(timestamp){
		let yoDate = new Date(Number(timestamp))
		let currentDate = new Date()
		let yo = currentDate.getFullYear() - yoDate.getFullYear()
		if(currentDate.getMonth() >= yoDate.getMonth() && currentDate.getDate() >= yoDate.getDate())
			return yo
		return yo-1
	}
}
export default BaseInfo;