import { toHaveDisplayValue } from '@testing-library/jest-dom/dist/matchers';
import React from 'react';
import '../App.css';
import { Button } from '@mui/material';
import { Select } from '@mui/material';
import { FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import { MenuItem } from '@mui/material';

class TestLanguage extends React.Component{
    render(){
        let styleFlagRu = { backgroundImage: `url(https://i.ibb.co/sW3cvyL/russia.png)` }
        let styleFlagEu = { backgroundImage: `url(https://i.ibb.co/9vJrzGw/united-kingdom.png)` }
        return( 
            <div>
                <FormControl variant="standard">
                    <Select
                        labelId='demo-simple-select-label'
                        id="demo-simple-select"
                        value={"RU"}
                        label="Age"
                        onChange={this.handleChange}
                        disableUnderline
                        >
                            <MenuItem value="RU"><div className='flag-app-bar' style={styleFlagRu}></div></MenuItem>
                            <MenuItem value="EU"><div className='flag-app-bar' style={styleFlagEu}></div></MenuItem>
                    </Select>
                </FormControl>
            </div>
        )
    }
}
export default TestLanguage;