import { toHaveDisplayValue } from '@testing-library/jest-dom/dist/matchers';
import React from 'react';
import '../App.css';
import { Button } from '@mui/material';
import { Select } from '@mui/material';
import { FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import { MenuItem } from '@mui/material';

class LanguageChange extends React.Component{
    constructor(props){
        super(props)
        this.handleChange = this.handleChange.bind(this)
        this.callbackToRussian = this.props.changeCallback.toRussian
        this.callbackToEnglish = this.props.changeCallback.toEnglish
    }
    handleChange(event){
        (event.target.value === "RU") ? this.callbackToRussian() : this.callbackToEnglish()
    }
    render(){
        let styleFlagRu = { backgroundImage: `url(https://i.ibb.co/sW3cvyL/russia.png)` }
        let styleFlagEu = { backgroundImage: `url(https://i.ibb.co/9vJrzGw/united-kingdom.png)` }
        let languageValue = (this.props.isEnglish) ? "EU" : "RU"
        let classNameSelector = this.props.classNameSelector
        let classNameFlag = this.props.classNameFlag

        return( 
            <div className={classNameSelector}>
                <FormControl variant="standard">
                    <Select
                        labelId="demo-simple-select-filled-label"
                        id="demo-simple-select"
                        value={languageValue}
                        label="Age"
                        onChange={this.handleChange}
                        disableUnderline>
                            <MenuItem value="RU"><div className={classNameFlag} style={styleFlagRu}></div></MenuItem>
                            <MenuItem value="EU"><div className={classNameFlag} style={styleFlagEu}></div></MenuItem>
                    </Select>
                </FormControl>
            </div>
        )
    }
}
export default LanguageChange;