import { Dialog } from "@mui/material";
import {DialogTitle} from "@mui/material";
import React from "react";
class ErrorDialog extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        let styleDialog = {
            padding: "20px"
        }
        let styleTitleError = {
            width: "max-width",
            textAlign: "center",
            color: "#c20000",
            fontFamily: "Arial"
        }
        let styleDescriptionError = {
            width: "max-width",
            textAlign: "center",
            color: "black",
            fontFamily: "Arial"
        }
        let error = (this.props.errorMsg === undefined) ? "-   " : this.props.errorMsg 
        return(
            <Dialog open={true} style={styleDialog}>
                <div style={styleDialog}>
                <h2 style={styleTitleError}>Error</h2>
                <h3 style={styleDescriptionError}>{error}</h3>
                </div>
            </Dialog>
        )
    }
}
export default ErrorDialog