import React from 'react';
import '../App.css';

class OneContact extends React.Component{
    constructor(props){
        super(props)
        this.toSite = this.toSite.bind(this)
    }
    toSite(){
        if(this.props.contact.linkAddress !== undefined){
            alert("Address go")
            window.location.href = this.props.contact.linkAddress
        }else{
            alert("Address copy")
        } 
    }

    render(){
        let contact = this.props.contact
        let icon = contact.icon
        let address = contact.address
        let linkAddress = contact.linkAddress
        let color = contact.color
        let contactIconStyle = {
            backgroundImage: `url(${icon})`
        }
        let contactTextStyle = {
            backgroundColor: color
        }
        return(
            <div className='container-contact'>
		        <div className='contact-text' style={contactTextStyle}>
			        <p>{address}</p>
		        </div>
		        <div className='div-contact-icon' style={contactIconStyle} onClick={this.toSite}></div>
	        </div>
        )
    }
}
export default OneContact;