import React from 'react'
import { FormControl, InputLabel, NativeSelect } from '@mui/material';
import AppBarContact from './AppBarContact';
import LanguageChange from './LanguageChange';
import '../App.css'
class AppBar extends React.Component{
    render(){
        let contacts = this.props.contacts
        return(
                <div className='top-bar-selector'>
                    {contacts.map(oneContact => <AppBarContact contact={oneContact}/>)}
                    <LanguageChange
                        isEnglish = {this.props.isEnglish}
                        changeCallback= {this.props.changeCallback}
                        classNameSelector={this.props.classNameSelector}
                        classNameFlag={this.props.classNameFlag}
                        />
                </div>
        )
    }
}
export default AppBar