import React from 'react'
import { FormControl, InputLabel, NativeSelect } from '@mui/material';
import '../App.css'
class AppBarContact extends React.Component{
    constructor(props){
        super(props)
        this.icon = props.contact.icon
        this.linkAddress = props.contact.linkAddress
    }
    render(){
        let style = {backgroundImage: `url(${this.icon})`}
        return(
            <a href={this.linkAddress} className='a-link-address'>
                <div className='contact-app-bar' style={style}></div>
            </a>
        )
    }
}
export default AppBarContact