import React from 'react'
import '../App.css';
import OneWorkExperience from './OneWorkExperience';

class WorkExperieceInfo extends React.Component{
    render(){
        let experienceArray = this.props.resume.experience
        return(
            <div className='div-work-experience'>
                {
                        experienceArray.map(oneExperience => <OneWorkExperience experience={oneExperience}/> )
                }
            </div>
        )
    }
}

export default WorkExperieceInfo;